body {
	min-width: 340px;
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
	color: #afafb2;
	font-size: 15px;
	line-height: 1.5;
	letter-spacing: 0.5px;
	box-sizing: border-box;
	background: #0E0E10; }

iframe {
	width: 100%; }

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0px;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden; }

*, *:before, *:after {
	box-sizing: border-box;
	outline: none; }

ul {
	list-style: none;
	margin: 0;
	padding: 0; }

@mixin c-grey0 () {
  color: #C9C9CB; }
@mixin c-grey1 () {
  color: #afafb2; }
@mixin c-grey2 () {
  color: #797983; }
@mixin c-grey3 () {
  color: #61616F; }

a {
	@include c-grey1 ();
	text-decoration: none; }
p {
	margin: 0; }

button {
	padding: 0;
	border: none;
	cursor: pointer; }

.bg-main-block {
	padding: 32px;
	border-radius: 4px;
	background: #17171A; }

.title-global {
	font-weight: 400;
	font-size: 30px;
	line-height: 140%;
	letter-spacing: 0.15px;
	color: #C9C9CB;
	margin: 0;
	margin-top: 16px; }

.subtitle-global {
	font-weight: 500;
	font-size: 20px;
	line-height: 140%;
	letter-spacing: 0.15px;
	color: #C9C9CB;
	padding: 14px 20px; }

.btn-global {
	display: flex;
	justify-content: center;
	align-items: center;
	background: #28282D;
	padding: 6px 14px;
	border: none;
	border-radius: 4px;
	font-size: 13px;
	line-height: 150%;
	letter-spacing: 0.25px;
	color: #797983;
	&:hover {
		background: #28282D;
		color: #fff; } }

.text-color--green {
	color: #37AD6D; }

.text-color--red {
	color: #D42C2C; }

.text-clip,
.text-clip-row {
	overflow: hidden;
	text-overflow: ellipsis; }

.text-clip {
	width: 100%;
	white-space: nowrap; }

.list-mark-global {
	position: relative;
	padding-left: 12px;
	&::before {
		content: '';
		position: absolute;
		top: calc(50% - 2px);
		left: 0;
		width: 4px;
		height: 4px;
		border-radius: 50%;
		background: #61616F; } }

.gradient-btn {
	background: linear-gradient(227.8deg, #FB6F43 3.56%, #B41431 92.44%);
	border-radius: 2px;
	font-size: 12px;
	font-weight: bold;
	letter-spacing: 1.5px;
	text-transform: uppercase;
	color: #FFFFFF;
	padding: 0 6px; }

@media (max-width: 900px) {
	.bg-main-block {
		padding: 15px; } }
