.authorization {
  display: none;
  position: fixed;
  top: calc(50% - 194px);
  left: calc(50% - 175px);
  width: 350px;
  background: #17171A;
  border-radius: 4px;
  padding-bottom: 32px;
  z-index: 100;
  &__overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(14, 14, 16, 0.7);
    z-index: 99;
  }
  &--active,
  &__overlay--active {
    display: block;
  }
  &__btn-close {
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    border: none;
    background: transparent;
    &:before,
    &:after {
      content: '';
      position: absolute;
      top: calc(50% - 8px);
      left: calc(50% - 1px);
      width: 2px;
      height: 16px;
      background: #AFAFB3;
      border-radius: 2px;
    }
    &:before {
      transform: rotate(45deg);
    }
    &:after {
      transform: rotate(-45deg);
    }
    &:hover:before,
    &:hover:after {
      background: #fff;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 17px;
    line-height: 135%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    letter-spacing: 0.25px;
    color: #C9C9CB;
    margin: 0;
    background: #1F1F23;
    border-radius: 4px 4px 0px 0px;
  }
  &__form {
    padding: 32px 20px;
    display: flex;
    flex-direction: column;
  }
  &__input {
    height: 40px;
    padding: 12px;
    background: #1F1F23;
    border: 1px solid #17171A;
    border-radius: 4px;
    margin-bottom: 12px;
    color: #fff;
    &::placeholder {
      color: #797983;
    }
    &:hover {
      background: #28282D;
      border-color: #39393E;
    }
    &:focus {
      background: #28282D;
      border-color: #61616F;
    }
  }
  &__checkbox {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    font-size: 13px;
    letter-spacing: 0.25px;
    color: #797983;
    cursor: pointer;
    &-label {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 22px;
      height: 22px;
      background: #1F1F23;
      border: 1px solid #17171A;
      border-radius: 4px;
      margin-right: 8px;
      svg {
        display: none;
      }
    }
    &:hover {
      color: #AFAFB3;
    }
    &:hover .authorization__checkbox-label {
      background: #28282D;
      border-color: #39393E;
      svg {
        display: block;
      }
    }
    &-input:checked ~ .authorization__checkbox {
      color: #AFAFB2;
      span {
        background: linear-gradient(227.8deg, #FB6F43 3.56%, #B41431 92.44%);
        border: none;
        svg {
          display: block;
          path {
            fill: #fff;
          }
        }
      }
    }
  }
  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    background: #28282D;
    border-radius: 4px;
    border: none;
    font-weight: bold;
    font-size: 12px;
    letter-spacing: 0.3px;
    color: #AFAFB3;
    svg {
      margin-left: 6px;
    }
    &:hover {
      background: linear-gradient(227.8deg, #FB6F43 3.56%, #B41431 92.44%);
      color: #fff;
      svg path {
        fill: #fff;
      }
    }
    &:active {
      background: #D52C2C;
      color: #fff;
      svg path {
        fill: #fff;
      }
    }
  }
  &__social {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: calc(50% - 6px);
      height: 40px;
      border-radius: 4px;
      font-size: 12px;
      letter-spacing: 0.3px;
      color: #fff;
      svg {
        margin-right: 4px;
        path {
          fill: #fff;
        }
      }
    }
    &--vk {
      background: #45668E;
    }
    &--steam {
      background: #00ADEE;
    }
  }
  &__question {
    display: flex;
    justify-content: center;
    margin-top: 12px;
    span {
      margin: 0 5px;
      color: #797983;
    }
    a {
      font-size: 13px;
      letter-spacing: 0.25px;
      color: #797983;
      &:hover {
        color: #fff;
      }
    }
  }
}

@media (max-width: 400px) {
  .authorization {
    width: 100%;
  }
}