.pagination {
  display: flex;
  align-items: center;
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 4px;
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    padding: 6px 9px;
    height: 30px;
    color: #797983;
    background: #1F1F23;
    border-radius: 4px;
    &:hover {
      color: #fff;
      svg path {
        fill: #fff;
      }
    }
    &--active {
      background: linear-gradient(227.8deg, #FB6F43 3.56%, #B41431 92.44%);
      color: #fff;
    }
    &--right svg {
      transform: rotate(180deg);
      margin-bottom: 1px;
    }
  }
}

@media (max-width: 600px) {
  .pagination {
    &__item {
      &--desk {
        display: none;
      }
    }
  }
}

@media (max-width: 400px) {
  .pagination {
    &__item {
      &--all {
        display: none;
      }
    }
  }
}