.news-page {
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__content {
    width: calc(100% - 324px);
    .index__news-list {
      margin-top: 0;
      margin-top: 24px;
    }
    .index__news-item {
      width: 32%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    margin-top: 24px;
    border-radius: 4px;
    background: #1F1F23;
    overflow: hidden;
  }
  &__item {
    display: flex;
  }
  &__link {
    font-weight: 500;
    padding: 19px 18px;
    &--active {
      color: #fff;
      background: #17171A;
    }
    &:hover {
      color: #fff;
    }
  }
  &__search {
    display: flex;
    width: 100%;
    border-radius: 4px;
    margin-top: 24px;
    input,
    button {
      height: 40px;
      border: none;
    }
    input {
      width: calc(100% - 40px);
      padding: 5px 12px;
      background: rgba(58, 58, 61, 0.5);
      border-radius: 4px 0px 0px 4px;
      font-size: 13px;
      color: #fff;
      &:hover {
        background: #3A3A3D;
      }
    }
    button {
      width: 40px;
      background: #28282D;
      border-radius: 0px 4px 4px 0px;
      &:hover {
        background: #1F1F23;
        svg path {
          fill: #fff;
        }
      }
    }
  }
}
@media (max-width: 1100px) {
  .news-page {
    .index__news-item {
      width: 48%;
    }
  }
}
@media (max-width: 940px) {
  .news-page {
    &__wrap {
      flex-direction: column;
    }
    &__content {
      width: 100%;
    }
    .index__news-item {
      width: 32%;
    }
    &__right-bar {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .component-block {
        margin-top: 24px;
      }
    }
  }
}

@media (max-width: 500px) {
  .news-page {
    .index__news-item {
      width: 100%;
    }
  }
}